@layer appui.component {
  .nz-widgetPanels-grip {
    background-color: transparent;
    align-items: center;
    justify-content: center;
    position: relative;
    display: flex;
    transition: 0.2s ease-out;
    user-select: none;
    -webkit-user-select: none;
  }
  .nz-widgetPanels-grip > .nz-handle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  .nz-widgetPanels-grip.nz-left {
    transition-property: background-color, right;
  }
  .nz-widgetPanels-grip.nz-right {
    transition-property: background-color, left;
  }
  .nz-widgetPanels-grip.nz-top {
    transition-property: background-color, bottom;
  }
  .nz-widgetPanels-grip.nz-left, .nz-widgetPanels-grip.nz-right {
    flex-direction: column;
    cursor: ew-resize;
    width: calc(0.75em * 0.5);
    height: 100%;
  }
  .nz-widgetPanels-grip.nz-left > .nz-handle, .nz-widgetPanels-grip.nz-right > .nz-handle {
    height: 100%;
    width: calc(0.75em + 1px);
  }
  .nz-widgetPanels-grip.nz-top, .nz-widgetPanels-grip.nz-bottom {
    cursor: ns-resize;
    width: 100%;
    height: calc(0.75em * 0.5);
  }
  .nz-widgetPanels-grip.nz-top > .nz-handle, .nz-widgetPanels-grip.nz-bottom > .nz-handle {
    height: calc(0.75em + 1px);
    width: 100%;
  }
  .nz-widgetPanels-grip:hover, .nz-widgetPanels-grip.nz-resizing {
    background-color: var(--buic-foreground-primary);
  }
  .nz-line-grip {
    display: none;
    align-items: center;
    justify-content: center;
    transition: opacity 0.2s ease-out, height 0.2s ease-out, width 0.2s ease-out;
  }
  .nz-captured .nz-line-grip, .nz-widgetPanels-grip.nz-resizing .nz-line-grip, .nz-collapsed .nz-widgetPanels-grip:hover .nz-line-grip {
    opacity: 0;
    height: 100%;
    width: 100%;
  }
  .nz-collapsed .nz-line-grip {
    display: flex;
    background-color: var(--buic-background-5);
    border: 1px solid var(--iui-color-border-subtle);
    border-radius: 999px;
    box-sizing: border-box;
    opacity: 1;
  }
  .nz-left .nz-line-grip {
    transform: translateX(1px);
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
  }
  .nz-right .nz-line-grip {
    transform: translateX(calc(1px * -1));
    border-top-right-radius: 0;
    border-bottom-right-radius: 0;
  }
  .nz-top .nz-line-grip {
    transform: translateY(1px);
    border-top-left-radius: 0;
    border-top-right-radius: 0;
  }
  .nz-bottom .nz-line-grip {
    transform: translateY(calc(1px * -1));
    border-bottom-left-radius: 0;
    border-bottom-right-radius: 0;
  }
  .nz-left .nz-line-grip, .nz-right .nz-line-grip {
    height: 20%;
    width: calc(0.75em - 2px);
  }
  .nz-left .nz-line-grip .nz-line-grip-detail, .nz-right .nz-line-grip .nz-line-grip-detail {
    width: 1px;
    height: 70%;
  }
  .nz-top .nz-line-grip, .nz-bottom .nz-line-grip {
    height: calc(0.75em - 2px);
    width: 20%;
  }
  .nz-top .nz-line-grip .nz-line-grip-detail, .nz-bottom .nz-line-grip .nz-line-grip-detail {
    width: 70%;
    height: 1px;
  }
  .nz-line-grip .nz-line-grip-detail {
    background-color: var(--iui-color-border-subtle);
  }
}