@layer appui.component {
  .nz-standardLayout {
    position: relative;
    height: 100%;
    background-color: var(--buic-background-widget-panel);
    overflow: hidden;
    display: grid;
    grid-template-columns: [lp-start] minmax(0, auto) [lp-end] 1fr [rp-start] minmax(0, auto) [rp-end];
    grid-template-rows: minmax(0, auto) [ts-end] minmax(0, auto) [tp-end] 1fr [bp-start] minmax(0, auto) [bp-end] minmax(0, auto);
    grid-template-areas: "ts ts ts" "tp tp tp" "lp cc rp" "bp bp bp" "sb sb sb";
  }
  .nz-standardLayout > .nz-appContent {
    grid-row: ts-end/bp-end;
    grid-column: 1/-1;
    position: relative;
  }
  .nz-standardLayout > .nz-toolSettings {
    grid-area: ts;
  }
  .nz-standardLayout > .nz-centerContent {
    grid-area: cc;
    pointer-events: none;
  }
  .nz-standardLayout > .nz-statusBar {
    grid-area: sb;
  }
  .nz-standardLayout.nz-pinned-left > .nz-appContent {
    grid-column-start: 2;
  }
  .nz-standardLayout.nz-pinned-right > .nz-appContent {
    grid-column-end: -2;
  }
  .nz-standardLayout.nz-pinned-top > .nz-appContent {
    grid-row-start: tp-end;
  }
  .nz-standardLayout.nz-pinned-bottom > .nz-appContent {
    grid-row-end: bp-start;
  }
  .nz-standardLayout_topPanel {
    grid-area: tp;
  }
  .nz-standardLayout_leftPanel {
    grid-area: lp;
  }
  .nz-standardLayout_rightPanel {
    grid-area: rp;
  }
  .nz-standardLayout_bottomPanel {
    grid-area: bp;
  }
}