.tree-header-button-dropdown-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: calc(var(--iui-size-2xs) + var(--iui-size-3xs));
}
.tree-header-button-dropdown-container .dropdown-item {
  width: 100%;
}
.tree-header-button-dropdown-container .dropdown-item > * {
  width: 100%;
  height: 100%;
}

.tree-widget-tree-header {
  --tree-header-height-inner: var(--tree-header-height, --iui-size-xl);
  --tree-header-contracted-width-inner: var(--tree-header-contracted-width, --iui-size-xl);
  display: flex;
  align-items: center;
  height: var(--tree-header-height-inner);
  justify-content: space-between;
}
.tree-widget-tree-header .tree-widget-search-box {
  width: calc(100% - var(--tree-header-contracted-width-inner));
}
.tree-widget-tree-header .tree-widget-search-box.contracted {
  width: unset;
}
.tree-widget-tree-header .tree-widget-search-box.contracted::before {
  border: none;
}
.tree-widget-tree-header .tree-widget-search-box .searchbox-stepping-count {
  font-size: var(--iui-size-s);
  color: var(--iui-color-text);
  margin-right: var(--iui-size-2xs);
}
.tree-widget-tree-header .button-container {
  width: calc(100% - var(--tree-header-contracted-width-inner));
}
.tree-widget-tree-header .button-container.contracted {
  width: var(--tree-header-contracted-width-inner);
}