.uifw-feedback-elementTooltip {
  pointer-events: none;
  width: max-content;
  font-size: var(--iui-font-size-1);
  border-color: var(--iui-color-border);
  border-radius: var(--iui-border-radius-1);
  border-style: solid;
  border-width: 1px;
}

.uifw-feedback-elementTooltip_container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  z-index: 18000;
}