/* Breakpoints */
.uifw-statusbar-labelIndicator.uifw-reversed {
  flex-direction: row-reverse;
}
.uifw-statusbar-labelIndicator > .uifw-label {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  cursor: default;
}