.uifw-statusbar-field {
  gap: var(--iui-size-xs);
  height: 100%;
  display: flex;
  align-items: center;
  box-sizing: border-box;
}
.uifw-statusbar-field.uifw-action {
  cursor: pointer;
}
.uifw-statusbar-field.uifw-action:hover {
  color: var(--iui-color-text-accent-hover);
}