.uifw-toolbar-group-toolGroup_container {
  flex: 1 1 100%;
  min-width: 0;
}
.uifw-toolbar-group-toolGroup_container.uifw-vertical {
  height: 100%;
}
.uifw-toolbar-group-toolGroup_container.uifw-end {
  display: flex;
  justify-content: flex-end;
}

.uifw-toolbar-group-toolGroup {
  display: flex;
  width: fit-content;
  height: fit-content;
  border-color: var(--iui-color-border-foreground);
  transition: border-color var(--iui-duration-1) ease;
  pointer-events: all;
}
@supports (backdrop-filter: blur(5px)) {
  .uifw-toolbar-group-toolGroup {
    background-color: hsl(var(--iui-color-background-hsl)/var(--iui-opacity-2));
    backdrop-filter: blur(5px);
  }
}
.uifw-toolbar-group-toolGroup.uifw-vertical {
  flex-direction: column;
}
.uifw-toolbar-group-toolGroup:hover, .uifw-toolbar-group-toolGroup:focus-within {
  border-color: var(--iui-color-border-foreground-hover);
}
.uifw-toolbar-group-toolGroup:empty {
  display: none;
}