#uifw-configurableui-wrapper {
  position: relative;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  font-size: var(--iui-font-size-1);
}

#uifw-frontstage-composer {
  width: 100%;
  height: 100%;
}

.uifw-unselectable {
  user-select: none;
  -webkit-user-select: none;
}

.uifw-centered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.uifw-dragdrop-layer {
  cursor: grabbing;
  position: fixed;
  pointer-events: none;
  z-index: 20000;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
}

.uifw-app-button-small {
  margin-bottom: 6px;
}

@layer appui.layout {
  .uifw-configurableui-portalContainer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    pointer-events: none;
  }
  .uifw-configurableui-portalContainer > * {
    pointer-events: all;
  }
}