/** Toolbar Opacity */
@layer appui.component {
  .nz-widget-tabBar {
    background: var(--iui-color-background-backdrop);
    display: flex;
    border-radius: 2px 2px 0 0;
    flex: 0 0 auto;
    position: relative;
    height: 2.1em;
  }
  .nz-widget-tabBar.nz-separator {
    border-bottom: 1px solid var(--iui-color-border);
  }
  .nz-widget-tabBar > .nz-handle {
    width: 100%;
    height: 100%;
    position: absolute;
    cursor: auto;
    user-select: none;
    -webkit-user-select: none;
  }
}