.uifw-accudraw-field-container {
  padding: 6px;
}
.uifw-accudraw-field-container.uifw-horizontal {
  max-width: 550px;
  display: flex;
  flex-direction: row;
}
.uifw-accudraw-field-container.uifw-vertical {
  max-width: 250px;
  display: grid;
  grid-template-columns: auto 1fr auto;
  row-gap: var(--iui-size-2xs);
}