.tw-tree-renderer {
  width: fit-content;
  min-width: 100%;
}
.tw-tree-renderer .tw-tree-node-checkbox-container {
  position: sticky;
  left: 0;
  z-index: 1;
  background-color: var(--iui-color-background);
  padding-left: calc(var(--iui-size-2xs) + var(--iui-size-3xs));
  height: calc(100% - var(--iui-size-2xs));
  display: flex;
  justify-content: center;
  align-items: center;
}
.tw-tree-renderer .tw-tree-node-checkbox::before {
  background-color: var(--iui-color-background);
  inset: calc(-1 * var(--iui-size-xs)) calc(-1 * var(--iui-size-xs) / 2);
  z-index: -1;
}
.tw-tree-renderer .tw-tree-node-action-buttons {
  position: sticky;
  right: 0;
  z-index: 1;
  background-color: var(--iui-color-background);
}
.tw-tree-renderer .tw-tree-node-content {
  overflow: visible;
}
.tw-tree-renderer .tw-tree-node.selected .tw-tree-node-checkbox-container,
.tw-tree-renderer .tw-tree-node.selected .tw-tree-node-checkbox::before,
.tw-tree-renderer .tw-tree-node.selected .tw-tree-node-action-buttons {
  background-color: var(--iui-color-background-accent-muted);
}
.tw-tree-renderer .tw-tree-node:not(.selected):hover .tw-tree-node-checkbox-container,
.tw-tree-renderer .tw-tree-node:not(.selected):hover .tw-tree-node-checkbox::before,
.tw-tree-renderer .tw-tree-node:not(.selected):hover .tw-tree-node-action-buttons {
  background-color: var(--iui-color-background-hover);
}