/** Toolbar Opacity */
.uifw-statusFields-toolAssistance-toolAssistanceField {
  width: 30ch;
  justify-content: flex-start;
}
.uifw-statusFields-toolAssistance-toolAssistanceField .prompt {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.uifw-statusFields-toolAssistance-toolAssistanceField .icon {
  font-size: 16px;
}

.uifw-toolassistance-key {
  color: var(--iui-color-text);
  background-color: var(--iui-color-background);
  border: 1px solid var(--iui-color-border);
  box-shadow: 0 1px 1px var(--iui-color-border), 0 1px 0 0 rgba(255, 255, 255, var(--iui-opacity-5)) inset;
  box-sizing: border-box;
  border-radius: var(--iui-border-radius-1);
  display: inline-block;
  font-size: 14px;
  font-weight: 600;
  line-height: 1;
  width: 20px;
  height: 20px;
  white-space: nowrap;
  cursor: default;
  text-transform: uppercase;
}
.uifw-toolassistance-key.uifw-toolassistance-key-small {
  font-size: 8px;
  width: 12px;
  height: 12px;
}
.uifw-toolassistance-key.uifw-toolassistance-key-medium {
  font-size: 12px;
  width: 18px;
  height: 18px;
}
.uifw-toolassistance-key.uifw-toolassistance-key-large {
  font-size: 11px;
  width: 36px;
  text-transform: none;
}

.uifw-toolassistance-key-group {
  display: inline-block;
  text-align: center;
  line-height: 0.714em;
}
.uifw-toolassistance-key-group .row1 {
  margin-left: 0;
  margin-bottom: 0;
}
.uifw-toolassistance-key-group .row2 {
  margin-top: 0;
}

.uifw-toolassistance-key-modifier {
  font-size: 9px;
  width: 24px;
  height: 18px;
  text-transform: none;
}

.uifw-toolassistance-icon-medium {
  width: 16px;
  height: 16px;
  display: inline-block;
  font-size: 16px;
  padding-left: 2px;
}

.uifw-toolassistance-icon-large {
  font-size: 22px;
}

.uifw-toolassistance-svg {
  width: 22px;
  height: 22px;
}

.uifw-toolassistance-svg-wide {
  width: 30px;
  height: 22px;
}

.uifw-toolassistance-svg-medium {
  width: 16px;
  height: 16px;
  display: inline-block;
  padding-left: 2px;
}

.uifw-toolassistance-svg-medium-wide {
  width: 24px;
  height: 16px;
  display: inline-block;
  padding-left: 1px;
}

.nz-footer-toolAssistance-instruction-image i {
  font-size: 20px;
}

.uifw-toolAssistance-content {
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 10px;
  padding-bottom: 10px;
}
.uifw-toolAssistance-content .uifw-toolAssistance-toggle-container {
  flex: 1;
  display: flex;
  box-sizing: border-box;
  align-items: center;
}
.uifw-toolAssistance-content .uifw-toolAssistance-toggle-container .uifw-toolAssistance-toggle-label {
  padding-left: 0.5em;
}

.uifw-toolAssistance-tabs {
  margin: 0;
  padding: 0;
  border: none;
  vertical-align: baseline;
  font-size: var(--iui-font-size-2);
  font-weight: var(--iui-font-weight-normal);
  line-height: var(--iui-size-l);
}
.uifw-toolAssistance-tabs > .uifw-tab {
  width: 50%;
  text-align: center;
}