.fb-group {
  --border-color: var(--iui-color-border);
  --fb-operator-width: 128px;
  --divider-width: var(--iui-size-2xs);
  --divider-background-color: var(--iui-color-border-subtle);
  gap: var(--iui-size-s);
}
.fb-group .fb-wrapper {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: var(--iui-size-xs);
}
.fb-group .fb-logical-operator {
  width: var(--fb-operator-width);
  height: 100%;
  text-align: center;
  background-color: var(--iui-color-background-hover);
  user-select: none;
  -webkit-user-select: none;
  display: grid;
  place-items: center;
  box-sizing: border-box;
  font-weight: var(--iui-font-weight-semibold);
}
.fb-group .fb-group-operator {
  border: 1px solid var(--border-color);
}