.uifw-statusFields-selectionCount {
  gap: var(--iui-size-2xs);
  display: flex;
  align-items: center;
  min-width: 3.75rem;
  margin: 0 var(--iui-size-xs);
}
.uifw-statusFields-selectionCount .icon {
  color: var(--iui-color-icon);
}
.uifw-statusFields-selectionCount.uifw-left-corner {
  margin-left: var(--iui-size-m);
}
.uifw-statusFields-selectionCount.uifw-right-corner {
  margin-right: var(--iui-size-m);
}