.uifw-view-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  width: calc(100% - var(--iui-size-s) * 4 - 80px);
  position: absolute;
  inset: auto 0 var(--iui-size-s) 0;
  margin-inline: auto;
  pointer-events: none;
  z-index: 1000;
}

.uifw-animation-overlay {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1000px;
  background-color: Hsl(var(--iui-color-background-hsl)/var(--iui-opacity-2));
  -webkit-backdrop-filter: saturate(1.8) blur(20px);
  backdrop-filter: saturate(1.8) blur(20px);
  border: 1px solid var(--iui-color-border);
  border-radius: var(--iui-border-radius-1);
  transition: background-color var(--iui-duration-1) ease;
  pointer-events: auto;
  container: solar-timeline/inline-size;
}
.uifw-animation-overlay:hover {
  background-color: Hsl(var(--iui-color-background-hsl)/var(--iui-opacity-1));
}