.timeline-component {
  width: 100%;
  min-width: 275px;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 8px 16px;
  box-sizing: border-box;
  gap: 8px;
}
.timeline-component .time-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.timeline-component .timeline-time {
  font-size: 12px;
  color: var(--iui-color-text-muted);
  text-align: center;
  min-height: inherit;
}
.timeline-component .timeline-duration-time {
  font-size: 14px;
  width: 40px;
  color: var(--iui-color-text);
  text-align: center;
  min-height: inherit;
}
.timeline-component .timeline-date {
  font-size: 12px;
  color: var(--iui-color-text-muted);
  padding-bottom: 4px;
}
.timeline-component .slider {
  position: relative;
  flex: 1;
  margin: 0 20px;
}