.uifw-statusBar {
  display: flex;
}
.uifw-statusBar.nz-safe-area-left {
  /* iOS 11 */
  padding-left: constant(safe-area-inset-left);
  /* iOS 11.2+ */
  padding-left: env(safe-area-inset-left);
}
.uifw-statusBar.nz-safe-area-right {
  /* iOS 11 */
  padding-right: constant(safe-area-inset-right);
  /* iOS 11.2+ */
  padding-right: env(safe-area-inset-right);
}
.uifw-statusBar.nz-safe-area-bottom {
  /* iOS 11 */
  padding-bottom: constant(safe-area-inset-bottom);
  /* iOS 11.2+ */
  padding-bottom: env(safe-area-inset-bottom);
}

.uifw-statusBar-space-between {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.uifw-statusBar-left {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
}

.uifw-statusBar-center {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.uifw-statusBar-right {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.uifw-statusBar-docked {
  width: 100%;
  background-color: var(--iui-color-background);
}
@media (min-width: 1300px) {
  .uifw-statusBar-docked .uifw-statusBar-item-container {
    padding-left: var(--iui-size-m);
  }
}
@media (max-width: 1299px) {
  .uifw-statusBar-docked .uifw-statusBar-item-container {
    padding-left: var(--iui-size-s);
  }
}
@media (max-width: 599px) {
  .uifw-statusBar-docked .uifw-statusBar-item-container {
    padding-left: var(--iui-size-xs);
  }
}
.uifw-statusBar-docked .uifw-statusBar-item-container:has(.uifw-statusBar-separator) {
  padding-left: 0;
}
.uifw-statusBar-docked .uifw-statusBar-item-container:has(.uifw-statusBar-separator) + .uifw-statusBar-item-container {
  padding-left: 0;
}
.uifw-statusBar-docked .uifw-statusBar-left .uifw-statusBar-item-container:first-child {
  padding-left: 0;
}

.uifw-statusBar-item-container {
  height: 100%;
  display: flex;
  flex-direction: row;
  align-content: center;
  align-items: center;
}